import { CalendarEvent } from "../Calendar";
import { Button } from "@/components/ui/button";
import { X } from "lucide-react";
import { cn } from "@/lib/utils";

interface CalendarCellProps {
  date: Date | null;
  events: CalendarEvent[];
  onDateClick: (date: Date) => void;
  onDeleteEvent: (eventId: string) => void;
  isToday: boolean;
}

export const CalendarCell = ({
  date,
  events,
  onDateClick,
  onDeleteEvent,
  isToday,
}: CalendarCellProps) => {
  if (!date) {
    return <div className="bg-calendar-cell min-h-[100px] p-2" />;
  }

  return (
    <div
      className={cn(
        "bg-calendar-cell min-h-[100px] p-2 cursor-pointer transition-colors hover:bg-calendar-cellHover",
        isToday && "bg-calendar-today"
      )}
      onClick={() => onDateClick(date)}
    >
      <div
        className={cn(
          "text-sm font-medium mb-1",
          isToday
            ? "text-primary font-bold"
            : "text-foreground"
        )}
      >
        {date.getDate()}
      </div>
      <div className="space-y-1">
        {events.map((event) => (
          <div
            key={event.id}
            className="group relative bg-primary/10 text-primary text-xs p-1 rounded truncate hover:bg-primary/20 transition-colors"
            onClick={(e) => e.stopPropagation()}
          >
            <span className="block truncate pr-5">{event.title}</span>
            <Button
              variant="ghost"
              size="icon"
              className="absolute right-0 top-0 h-5 w-5 opacity-0 group-hover:opacity-100 transition-opacity"
              onClick={(e) => {
                e.stopPropagation();
                onDeleteEvent(event.id);
              }}
            >
              <X className="h-3 w-3" />
            </Button>
          </div>
        ))}
      </div>
    </div>
  );
};
